export default function ScreenPortfolio(props) {
  const { i18n, t, } = useTranslation('portfolio');
  const [currentTag, setCurrentTag] = useState({ id: 'all', name: 'All', });

  const [posts, setPosts] = useState(postsData);
  useEffect(() => {
    const posts = postsData.filter((item) => item.tags.includes(currentTag.id));
    setPosts(posts);
  }, [currentTag]);

  return (
    <section id='portfolio' className='section-portfolio'>
      <br />
      <div className='round-portfolio round-portfolio-1'></div>
      <div className='round-portfolio round-portfolio-2'></div>
      <div className='container'>
        <h2 className='title'>{t('Portfolio')}</h2>
        <p className='desc'>{t('portfolioDescription')}</p>
        <div className='portfolio__tabs'>
          {tags.map((item) =>
            <CommonButton
              key={item.id}
              className='portfolio__tab'
              size='small'
              variant={currentTag.id === item.id ? 'gradient' : 'base'}
              active={currentTag.id === item.id ? true : false}
              onClick={() => setCurrentTag(item)}
            >
              {t(item.name)}
            </CommonButton>
          )}
        </div>

        <div className='row portfolio'>
          {posts.slice(0, 1).map((item) => {
            return (
              <div
                key={item.id}
                className='col-12 col-lg-8'
              >
                <div className='portfolio'>
                  <div className='portfolio-item portfolio-item__big'>
                    {item.img && <span className='portfolio-item__img'>
                      {item.img(item, i18n)}
                    </span>}

                    <div className='portfolio-content'>
                      <div className='portfolio-item__date'>
                        <span>{new Date(item.date).toLocaleDateString(i18n.language)}</span>
                      </div>
                      {item.icon && <span className='portfolio-item__icon'>
                        <img
                          alt={item.title[i18n.language]}
                          title={item.title[i18n.language]}
                          src={item.icon}
                        />
                      </span>}
                      <div className='portfolio-item__title'>{item.title[i18n.language]}</div>
                      <div className='portfolio-item__desc'>
                        {item.desc[i18n.language]}
                      </div>
                      <div className='portfolio-item__link'>
                        <InnerLink
                          to={item.link}
                          text={t('Read more')}
                        />
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            );
          })}

          {posts.slice(1).map((item) => {
            return (
              <div
                key={item.id}
                className='col-12 col-lg-4'
              >
                <div className='portfolio'>
                  <div className='portfolio-item'>
                    {item.img && <span className='portfolio-item__img'>
                      {item.img(item, i18n)}
                    </span>}
                    <div className='portfolio-item__date'>
                      <span>{new Date(item.date).toLocaleDateString(i18n.language)}</span>
                    </div>
                    <div className='portfolio-item__title'>{item.title[i18n.language]}</div>
                    <div className='portfolio-item__desc'>
                      {item.desc[i18n.language]}
                    </div>
                    <div className='portfolio-item__link'>
                      <InnerLink
                        to={item.link}
                        text={t('Read more')}
                      />
                    </div>
                  </div>
                </div>
              </div>
            );
          })}
        </div>

        <div
          className='portfolio__more'
        //className='portfolio__more d-md-none'
        >
          <Link to='/portfolio'>
            <CommonButton
              className=''
              size='full'
              variant='gradient'
            >
              {t('More')}
            </CommonButton>
          </Link>
        </div>
      </div>
    </section>
  );
};
